import markedSite from './../../../static/data/markedSite'
import searchService from './../../../static/data/searchService'
export function queryMarkedSite () {
  return new Promise((resolve, reject) => {
    resolve(markedSite)
  })
}
export function querySearchService () {
  return new Promise((resolve, reject) => {
    resolve(searchService)
  })
}



